
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";



function Banner() {
   const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  return (
    <div className="slider m-0 p-0">
      <Slider {...settings}>
          <div>
            <img  className="w-full max-h-screen" src="https://www.kannammacooks.com/wp-content/uploads/masala-poori-homemade-chaat-street-side-recipe-5.jpg" />
          </div>
          <div>
            <img className="w-full max-h-screen" src="https://www.kannammacooks.com/wp-content/uploads/masala-poori-homemade-chaat-street-side-recipe-5.jpg" />
          </div>
          <div>
            <img  className="w-full max-h-screen" src="https://www.kannammacooks.com/wp-content/uploads/masala-poori-homemade-chaat-street-side-recipe-5.jpg" />
          </div>
          <div>
            <img  className="w-full max-h-screen"src="https://www.kannammacooks.com/wp-content/uploads/masala-poori-homemade-chaat-street-side-recipe-5.jpg" />
          </div>
        </Slider>
    </div>
  );
}
export default Banner;


